import { Button, FormControlLabel, Switch } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import toast from "react-hot-toast";
import { FontList } from "../../const/fonts";
import { readDeviceInfo } from "../../services";
import {
  readSettings,
  toggleMarqueeOverlays,
  toggleTextOnMainPanel,
  toggleVideoMarquees,
  updateDeviceFont,
  updatePixelcadeDotTypeSPI,
  updatePixelcadeMiniDisplayTypeI2C,
  updatePixelcadeDeviceOLEDMode,
  toggleMiniOLEDGlitchMode,
  updateDeviceName,
  updateFirmware,
  updateFontColor,
} from "../../services/settings";
import { HexColorPicker } from "react-colorful";
import { Select, CaretIcon, ModalCloseButton } from "react-responsive-select";

// for default styles...
import "react-responsive-select/dist/react-responsive-select.css";

type SettingType = {
  font: string;
  SubDisplayScrollingOnly: boolean;
  ImageMarqueesOnly: boolean;
  SubDisplayNumModules: number;
  FontColor: string;
  marqueeOverlays: boolean;
  PixelcadeDotType: string;
  PixelcadeMiniDisplayType: string;
  PixelcadeOLEDMode: string;
  PixelcadeOLEDGlitch: boolean;
};

const DeviceSettings: React.FC = () => {
    const [deviceName, setDeviceName] = useState<string>("");
    const [settings, setSettings] = useState<SettingType | undefined>();

    useEffect(() => {
        readDeviceInfo().then((data) => setDeviceName(data.name));
        readSettings()
            .then((data) => {
                setSettings(data);
            })
            .catch();
    }, []);

    const getRebootMessage = (message: string) => {
        return (
            <div>
                <p>{message}</p>
            </div>
        );
    };
    const updateImageMarqueesOnly = () => {
        if (settings) {
            toast.promise(
                toggleVideoMarquees(!settings.ImageMarqueesOnly),
                {
                    loading: "Updating video marquees settings",
                    success: () => {
                        setSettings({
                            ...settings,
                            ImageMarqueesOnly: !settings.ImageMarqueesOnly,
                        });
                        return getRebootMessage(
                            `Video marquees settings updated`
                        );
                    },
                    error: "Setting failed",
                },
                { duration: 4000 }
            );
        }
    };

    const updateMiniOLEDGlitchMode = () => {
      if (settings) {
          toast.promise(
            toggleMiniOLEDGlitchMode(!settings.PixelcadeOLEDGlitch),
              {
                  loading: "Updating Mini OLED Glitch Mode",
                  success: () => {
                      setSettings({
                          ...settings,
                          PixelcadeOLEDGlitch: !settings.PixelcadeOLEDGlitch,
                      });
                      return getRebootMessage(
                          `Mini OLED Glitch Mode Updated`
                      );
                  },
                  error: "Mini OLED Glitch Mode Update Failed",
              },
              { duration: 4000 }
          );
      }
  };
    

    const updateTextOnMainPanel = () => {
        if (settings) {
            toast.promise(
                toggleTextOnMainPanel(!settings.SubDisplayScrollingOnly),
                {
                    loading: "Updating Text scrolling settings",
                    success: () => {
                        setSettings({
                            ...settings,
                            SubDisplayScrollingOnly:
                                !settings.SubDisplayScrollingOnly,
                        });
                        return getRebootMessage(
                            `Text scrolling settings updated`
                        );
                    },
                    error: "Setting failed",
                },
                { duration: 4000 }
            );
        }
    };

    const updateName = () => {
        toast.promise(
            updateDeviceName(deviceName),
            {
                loading: "Updating Device Name",
                success: () => {
                    return getRebootMessage(`Device Name updated`);
                },
                error: "Setting failed",
            },
            { duration: 4000 }
        );
    };

    const updateFont = () => {
        if (settings) {
            toast.promise(
                updateDeviceFont(settings.font),
                {
                    loading: "Updating Device Font",
                    success: "Font updated",
                    error: "Setting failed",
                },
                { duration: 4000 }
            );
        }
    };

   const updatePixelcadeDotType = () => {
      if (settings) {
          toast.promise(
              updatePixelcadeDotTypeSPI(settings.PixelcadeDotType),
              {
                  loading: "Updating Pixelcade Sub-Display Type",
                  success: "Sub-Display Type updated",
                  error: "Sub-Display Type Updated Failed",
              },
              { duration: 4000 }
          );
      }
  }; 

  const updatePixelcadeMiniDisplayType = () => {
    if (settings) {
        toast.promise(
          updatePixelcadeMiniDisplayTypeI2C(settings.PixelcadeMiniDisplayType),
            {
                loading: "Updating Pixelcade Mini Display Type",
                success: "Pixelcade Mini Display Type Updated",
                error: "Pixelcade Mini Display Updated Failed",
            },
            { duration: 4000 }
        );
    }
}; 

const updatePixelcadeOLEDMode = () => {
  if (settings) {
      toast.promise(
        updatePixelcadeDeviceOLEDMode(settings.PixelcadeOLEDMode),
          {
              loading: "Updating Pixelcade Mini OLED Mode",
              success: "Pixelcade Mini OLED Mode Updated",
              error: "Pixelcade Mini OLED Mode Update Failed",
          },
          { duration: 4000 }
      );
  }
}; 

  const updateColor = () => {
        if (settings) {
            toast.promise(
                updateFontColor(settings.FontColor),
                {
                    loading: "Updating Font Color",
                    success: "Font color updated",
                    error: "Setting failed",
                },
                { duration: 4000 }
            );
        }
    };

  const getFonts = () => {
    return FontList.map((font) => {
      return {
        value: font,
        text: font,
      };
    });
  };

  const updateMarqueeOverlay = () => {
    if (settings) {
      toast.promise(
        toggleMarqueeOverlays(!settings.marqueeOverlays),
        {
          loading: "Updating Marquee Overlay settings",
          success: () => {
            setSettings({
              ...settings,
              marqueeOverlays: !settings.marqueeOverlays,
            });
            return getRebootMessage(`Marquee Overlay settings updated`);
          },
          error: "Setting failed",
        },
        { duration: 4000 }
      );
    }
  };

  const updateSystem = () => {
    toast.promise(
      updateFirmware(),
      {
        loading: "Pixelcade LCD Update Started",
        success: () => {
          return `Please wait until your see the Update Complete message on your Pixelcade before doing anything else.`;
        },
        error: "failed",
      },
      { duration: 4000 }
    );
  };
  
  return (
    <div className="fade-in">
      {settings && (
        <div className="layout-grid" style={{ marginTop: "0.5rem" }}>
          <div
            className="settings"
            style={{ display: "flex", flexDirection: "column" }}
          >
            <label style={{ margin: "1rem 0 .5rem 0" }}>Device Name</label>

            <input
              style={{
                height: 36,
                fontSize: "1.2rem",
                padding: "0 0.4rem",
              }}
              type="text"
              id="fname"
              name="fname"
              value={deviceName}
              onChange={(event) => {
                setDeviceName(event.target.value);
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updateName()}
              >
                Update
              </Button>
            </div>
          </div>
          <div className="settings">
            <p>
              When disabled, Pixelcade will not show video marquees and will show static marquees only.
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={!settings.ImageMarqueesOnly}
                  onChange={() => updateImageMarqueesOnly()}
                  color="primary"
                />
              }
              label="Show Video Marquees"
            />
          </div>
          <div className="settings">
            <p>
              Disabling splash screens means Pixelcade will not display now playing splash screens, high scores, or marquee overlays. <br></br><br></br>Only a Pixelcade LED marquee
              (if connected) or a Pixelcade Dot sub-display (if connected) will
              display scrolling text for now playing game launches and high scores.
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={settings.SubDisplayScrollingOnly}
                  onChange={() => updateTextOnMainPanel()}
                  color="primary"
                />
              }
              label="Disable Splash Screens"
            />
          </div>

          <div className="settings">
            <label style={{ margin: ".5rem 0 .5rem 0" }}>Font</label>
            <Select
              selectedValue={settings.font}
              name="font"
              options={getFonts()}
              caretIcon={<CaretIcon />}
              modalCloseButton={<ModalCloseButton />}
              onChange={(changes: any) => {
                const font = changes.value;
                setSettings({ ...settings, font });
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updateFont()}
              >
                Update
              </Button>
            </div>
          </div>

          <div className="settings">
          <p>
              Font colors are NOT in use at this time. The font color will always be white. 
            </p>
            <label style={{ margin: ".5rem 0 .5rem 0" }}>Font Color</label>
            <HexColorPicker
              color={settings.FontColor}
              onChange={(thing) =>
                setSettings({
                  ...settings,
                  FontColor: thing.replace("#", ""),
                })
              }
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updateColor()}
              >
                Update
              </Button>
            </div>
          </div>
          <div className="settings">
            <p>
              When disabled, Pixelcade will not show overlays such as high
              scores in a small bar on the bottom of game marquees.
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={settings.marqueeOverlays}
                  onChange={() => updateMarqueeOverlay()}
                  color="primary"
                />
              }
              label="Show Marquee Overlays"
            />
          </div>

          <div className="settings">
            <label style={{ margin: ".5rem 0 .5rem 0" }}>Pixelcade Dot Sub-Display Type</label>
            <Select
              selectedValue={settings.PixelcadeDotType}
              name="PixelcadeDotType"
              options={[
                { text: "Matrix", value: "matrix" },
                { text: "7 Segment", value: "7segment" }
              ]}
              caretIcon={<CaretIcon />}
              modalCloseButton={<ModalCloseButton />}
              onChange={(changes: any) => {
                const PixelcadeDotType = changes.value;
                setSettings({ ...settings, PixelcadeDotType });
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updatePixelcadeDotType()}
              >
                Update
              </Button>
            </div>
          </div>

          <div className="settings">
            <label style={{ margin: ".5rem 0 .5rem 0" }}>Pixelcade Mini Dot Sub-Display Type</label>
            <Select
              selectedValue={settings.PixelcadeMiniDisplayType}
              name="PixelcadeMiniDisplayType"
              options={[
                { text: "Mini OLED", value: "oled" },
                { text: "Mini LCD 4 Lines", value: "lcd4line" },
                { text: "Mini LCD 2 Lines", value: "lcd2line" }
              ]}
              caretIcon={<CaretIcon />}
              modalCloseButton={<ModalCloseButton />}
              onChange={(changes: any) => {
                const PixelcadeMiniDisplayType = changes.value;
                setSettings({ ...settings, PixelcadeMiniDisplayType });
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updatePixelcadeMiniDisplayType()}
              >
                Update
              </Button>
            </div>
          </div>

          <div className="settings">
            <label style={{ margin: ".5rem 0 .5rem 0" }}>Pixelcade Mini OLED Mode</label>
            <Select
              selectedValue={settings.PixelcadeOLEDMode}
              name="PixelcadeOLEDMode"
              options={[
                { text: "Marquee Mode", value: "marquee" },
                { text: "Metadata Mode", value: "metadata" }
              ]}
              caretIcon={<CaretIcon />}
              modalCloseButton={<ModalCloseButton />}
              onChange={(changes: any) => {
                const PixelcadeOLEDMode = changes.value;
                setSettings({ ...settings, PixelcadeOLEDMode });
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updatePixelcadeOLEDMode()}
              >
                Update
              </Button>
            </div>
          </div>

          <div className="settings">
            <p>
           Pixelcade Mini OLED Glitch Mode
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={settings.PixelcadeOLEDGlitch}
                  onChange={() => updateMiniOLEDGlitchMode()}
                  color="primary"
                />
              }
              label="Enable Pixelcade Mini OLED Glitch Mode"
            />
          </div>

          <div
            className="settings"
            style={{ display: "flex", flexDirection: "column" }}
          >
            <label style={{ margin: "1rem 0 .5rem 0" }}>
            System and Artwork Updates
            </label>

            <p>
              Check for new system and artwork updates. Pixelcade will display
              status messages including a message if your Pixelcade is already
              up to date. Please note that updates can take up to 15 minutes or more
              depending on your Internect connection speed.
            </p>

            <div />
            <Button
              style={{ marginTop: "1.2rem" }}
              variant="contained"
              color="primary"
              onClick={() => updateSystem()}
            >
              Check for Updates
            </Button>
          </div>
        </div>
      )}
    </div>
  );
};

export default DeviceSettings;
